import Swiper from 'swiper';
export default {
	data() {
		return {
			swiperOption: {
				slidesPerView: 'auto',
				centeredSlides: true,
				spaceBetween: 10,
				loop: true,
				speed: 600 //config参数同swiper4,与官网一致
			}
		};
	},
	// 当页面加载时判断是否登录；
	mounted() {
		var mySwiper = new Swiper('.swiper-container', {
			autoplay: true,

			loop: true,

			// 如果需要分页器

			pagination: {
				el: '.swiper-pagination'
			},

			// 如果需要前进后退按钮

			navigation: {
				nextEl: '.swiper-button-next',

				prevEl: '.swiper-button-prev'
			}

			// 如果需要滚动条
		});
	},

	components: {}
};
